import React, { useContext, useEffect, useState } from "react";
import Abilities from "./components/Abilities/Abilities";
import Hobbies from "./components/Hobbies/Hobbies";
import Home from "./components/Home/Home";
import Layout from "./components/Layout/Layout";
import NavBar from "./components/NavBar/NavBar";
import Proyects from "./components/Projects/Proyects";
import LoadingDots from "./components/Styles/LoadingDots";
import SectionAnimation from "./components/Styles/SectionAnimation";
import WebsRestaurantes from "./components/WebsRestaurantes/WebsRestaurantes";
import ThemeProvider from "./Context/ThemeProvider";
import "./index.css";

const App = () => {
  return (
    <>
      <ThemeProvider>
        <Layout>
          <NavBar />
          <Home />
          <Proyects />
          <Abilities />
          <SectionAnimation orientation={false}>
            <Hobbies />
          </SectionAnimation>
        </Layout>
      </ThemeProvider>
    </>
  );
};

export default App;
